import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Proposta_candidato } from '../../../modelo/Proposta_candidato'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'proposta_candidato-dados-edit',   templateUrl: 'proposta_candidato_dadosEdit.component.html' }) export class Proposta_candidatoDadosEditComponent implements OnInit {    proposta_candidato: Proposta_candidato;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.proposta_candidato.Proposta_candidato";                 let id = +params['id'];         this.proposta_candidato = new Proposta_candidato();         this.proposta_candidato.cd_proposta = id;         o.objetoJson = this.proposta_candidato;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a proposta_candidato. ';       }      this.proposta_candidato = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


